import "core-js/modules/es.array.push.js";
import { store } from "@/store";
import axios from "axios";
import { ref } from "vue";
import { showFailToast, showSuccessToast } from "vant";
// const onCancel = () => showToast('取消');
// const onChange = ({ selectedOptions }) => {
//   showToast(`当前值: ${selectedOptions.join(',')}`);
// };

const columns = [{
  text: '你的生日是',
  value: 'birthday'
}, {
  text: '你的宠物的名字',
  value: 'petName'
}, {
  text: '你父亲或母亲姓名',
  value: 'parentName'
}, {
  text: '你小学学校名字',
  value: 'school'
}, {
  text: '你高中学校名字',
  value: 'highSchool'
}];
const showPicker = ref(false);
const question = ref('');
const onConfirm = ({
  selectedOptions
}) => {
  showPicker.value = false;
  question.value = selectedOptions[0].text;
};

//头像

export default {
  name: 'App',
  data() {
    return {
      // 文件上传用
      fileList: [],
      //默认是一个空数组
      imageData: {},
      isShow: false,
      showList: false,
      params: {
        userName: '',
        avatar: '',
        nickName: '',
        password: '',
        password2: '',
        email: '',
        phone: '',
        question: '',
        answer: ''
      },
      store,
      columns,
      onConfirm,
      question,
      showPicker
    };
  },
  mounted() {},
  methods: {
    onClickLeft() {
      this.$router.go(-1);
    },
    submit() {
      let that = this;
      this.params.question = question;
      axios.post("/user/register", this.params).then(res => {
        console.log(res);
        if (res.data.code === 3001) {
          this.store.userInfo = res.data.data;
          // sessionStorage.setItem("userInfo",JSON.stringify(res.data.data))
          showSuccessToast(res.data.msg);
          // console.log("session存："+JSON.stringify(res.data.data))
          // this.store.isLogin=true
          // 两秒后跳转到首页
          setTimeout(function () {
            console.log("跳转");
            that.$router.push("/login");
          }, 2000);
        } else {
          showFailToast(res.data.msg);
        }
      });
      console.log(this.params);
    },
    beforeRead(file) {
      // 仅支持jpg，png，gif格式的图片
      const isJPG = file.type === 'image/jpeg';
      const isPNG = file.type === 'image/png';
      const isGIF = file.type === 'image/gif';
      console.log("图片类型：" + file.type);
      const isLt1M = file.size / 1024 / 1024 < 1;
      if (!isJPG && !isPNG && !isGIF) {
        showFailToast('上传头像图片只能是 JPG/PNG/GIF 格式!');
      }
      if (!isLt1M) {
        showFailToast('上传头像图片大小不能超过 1MB!');
      }
      return isLt1M;
    },
    afterRead(file) {
      let that = this;
      // 此时可以自行将文件上传至服务器
      console.log(file);

      //在这块创建FormData对象
      // FormData 对象的使用：
      // 1.用一些键值对来模拟一系列表单控件：即把form中所有表单元素的name与value组装成一个queryString
      // 2. 异步上传二进制文件。
      //上传图片需要转换二进制这里要用到FormData
      const forms = new FormData();
      //这里的file表示给后台传的属性名字，这里看接口文档需要传的的属性
      forms.append("file", file.file); // 获取上传图片信息

      //向后端发送相应的请求
      //这块的url是具体的交互接口
      //headers是上传图片需要用到的响应头，此处的token是后端那边给设置的，所以我请求的时候需要带上token，
      //token根据实际情况自行添加
      axios.post("/user/upload", forms, '', {
        headers: {
          "content-type": "multipart/form-data"
        }
      }).then(res => {
        //如果传入的响应状态码为200，则成功将文件发送给后台
        if (res.data.code === 1001) {
          console.log("上传成功!!!!!!!!!!!!!!!!!!!");
          console.log(res);
          that.params.avatar = res.data.data.filePath;
          console.log(">>>>>>>头像地址：" + that.params.avatar);
          //this.imageData = res.data.showapi_res_body;
          //this.isShow = false;
          //this.showList = true;
          //Toast(res.data.showapi_res_body.remark);
        } else {
          //Toast(res.data.msg);
          //this.isShow = false;
          showFailToast(res.data.msg); //这块是请求失败后台给返回的相应的数据
        }
      });
    }

    // onConfirm(columns) {
    //   this.params.question = this.$refs.pickerRef.title;
    //   this.showPicker = false;
    //
    //   //   columns[0].text
    // },
  },

  components: {}
};